  --secondary-font: "TimesNewRoman", serif;
  --primary-bg-color-dark: #0e0c07;
  --primary-bg-color-light: #fffdee;
  --secondary-bg-color-light: #f0ecd6;
  --accent-color-theme-dark: #f3c653;
  --accent-color-theme-light: #c79002;
  --primary-text-color-theme-dark: #fff;
  --secondary-text-color-theme-dark: #bababa;
